/* #Base (1180 Grid)
================================================== */

.container {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  padding: 0;
}

.container .column,
.container .columns {
  float: left;
  display: inline;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 30px;
}

.row {
  margin-bottom: 20px;
}


/* Nested Column Classes */

.column.alpha,
.columns.alpha {
  margin-left: 0;
}

.column.omega,
.columns.omega {
  margin-right: 0;
}


/* Base Grid */

.container .one.column,
.container .one.columns {
  width: 55px;
}

.container .two.columns {
  width: 130px;
}

.container .three.columns {
  width: 205px;
}

.container .four.columns {
  width: 280px;
}

.container .five.columns {
  width: 355px;
}

.container .six.columns {
  width: 430px;
}

.container .seven.columns {
  /*		width: 505px; */
  width: 100%
}

.container .eight.columns {
  width: 580px;
}

.container .nine.columns {
  width: 655px;
}

.container .ten.columns {
  width: 730px;
}

.container .eleven.columns {
  width: 805px;
}

.container .twelve.columns {
  width: 880px;
}

.container .thirteen.columns {
  width: 955px;
}

.container .fourteen.columns {
  width: 1030px;
}

.container .fifteen.columns {
  width: 1105px;
}

.container .sixteen.columns {
  width: 1180px;
}

.container .one-third.column {
  width: 380px;
}

.container .two-thirds.column {
  width: 780px;
}

body.boxed {
  width: 1340px;
}

@media only screen and (min-width: 1289px) and (max-width: 1360px) {
  body.boxed {
    width: 100%;
  }
}


