/* ------------------------------------------------------------------- */


/* Header
---------------------------------------------------------------------- */

header {
  height: 90px;
  background-color: #fff;
}

#logo {
  margin-left: 0px;
  display: inline-block;
  float: left;
  margin-top: 24px;
}

#logo a {
  color: #ffb400;
}

#logo a:hover {
  color: grey;
}


/* Full-Width Header Styles */

header.full-width .container,
header.full-width .container .sixteen.columns {
  width: 100%;
  padding-left: 20px;
  box-sizing: border-box;
  float: left;
  margin: 0;
}

header.full-width #navigation ul.float-right {
  right: 35px;
}


/* Alternative Header & Menu style*/

.alternative #navigation {
  width: 100%;
  border-top: 1px solid #e0e0e0;
  margin: 27px 0 0 0;
  padding: 24px 0 12px 0;
}

.alternative #navigation ul {
  margin: 0;
}

.alternative .menu ul li {
  margin: 0 10px 0 0;
}

.alternative #navigation ul.float-right {
  top: 27px;
}


/* ---------------------------------- */


/* Transparent Header
------------------------------------- */

header.transparent {
  position: absolute;
  background: transparent;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: inset 0px -1px 0px 0px rgba(255, 255, 255, 0.2);
}

body.boxed #wrapper {
  position: relative;
}

header.transparent .menu ul li a {
  color: #fff;
  font-size: 13px;
  letter-spacing: 0.5px;
  line-height: 19px;
}

header.transparent .menu ul ul li a {
  color: #959595;
  font-size: 14px;
  letter-spacing: 0px;
}

header.transparent ul.float-right li a {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}

header.transparent ul.float-right li a:hover {
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
}

header.transparent .menu ul ul li a:hover {
  color: #fff;
}

header.transparent .sf-with-ul::after {
  color: rgba(255, 255, 255, 0.8);
}

#banner.with-transparent-header {
  max-height: 710px;
  background-attachment: fixed;
}

#banner.with-transparent-header .search-container {
  padding: 190px 0;
  margin-top: 45px;
}

#banner.with-transparent-header:before {
  background-color: rgba(42, 46, 50, 0.7);
}

header.transparent.alternative #navigation {
  border-top: none;
}

header.transparent #logo {
  border-bottom: none;
}


/* Smaller than standard 1180 (devices and browsers) */

@media only screen and (min-width: 960px) and (max-width: 1289px) {
  #banner.with-transparent-header .search-container {
    margin-top: 65px;
  }
}


/* Tablets */

@media only screen and (max-width: 990px) {
  header.transparent.alternative #navigation ul.float-right a {
    color: #fff;
    background: transparent;
  }
  header.transparent #navigation ul.float-right {
    right: -15px;
    top: 15px;
    position: absolute;
    text-align: right;
  }
  header.transparent a.menu-trigger {
    background: rgba(255, 255, 255, 0.2);
  }
  header.transparent {
    box-shadow: none;
  }
  #banner.with-transparent-header .search-container {
    margin-top: 65px;
  }
}


/* Mobiles */

@media only screen and (max-width: 767px) {
  header.transparent #logo {
    padding-bottom: 0;
  }
  header.transparent #navigation ul.float-right {
    right: -15px;
    top: 0;
    position: relative;
    text-align: center;
  }
  #banner.with-transparent-header .search-container {
    padding: 190px 0;
    margin-top: 35px;
  }
  #banner.with-transparent-header {
    max-height: 760px;
  }
  #logo img {
    margin: 35px auto 10px auto;
  }
}


/* White dropdown for transparent header */

header.transparent .menu ul ul {
  background-color: #fff;
}

header.transparent .menu ul ul:before {
  color: #fff;
}

header.transparent .menu ul li.sfHover ul li a.sf-with-ul,
header.transparent .menu ul ul li a {
  color: #808080;
  background-color: #fff;
}

header.transparent .menu ul ul li a:hover {
  color: #333;
}


/* ---------------------------------- */


/* Sticky Header
------------------------------------- */

.sticky-header.headroom--pinned.cloned,
.sticky-header.headroom--unpinned.cloned,
.sticky-header.headroom--top.cloned {
  transform: translateY(-100%);
  transition: all 0.25s ease-in-out;
}

.sticky-header.cloned {
  background-color: transparent;
  position: fixed !important;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  background-color: #fff;
  border-bottom: none;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.22);
  width: 100%;
  z-index: 999999;
  max-height: 80px;
}

.sticky-header.cloned.headroom--not-top.headroom--unpinned,
.sticky-header.cloned.headroom--not-top.headroom--pinned {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
  transform: translateY(0%);
}

.sticky-header.cloned .menu ul.float-right,
.sticky-header.cloned .menu ul#responsive {
  margin: 21px 0;
}

.sticky-header.cloned #logo img {
  max-height: 42px;
  width: auto;
  margin: 20px 30px 20px 0;
}


/* ---------------------------------- */


/* Navigation
------------------------------------- */

#navigation {
  float: left;
  margin-right: 40px;
}

#navigation ul.float-right {
  right: 10px;
  top: 0;
  position: absolute;
}

#navigation ul.float-right li a i {
  margin-right: 2px;
}

.js .selectnav {
  display: none;
}

.menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  text-transform: uppercase;
  margin: 26px 0 0 25px;
}

.menu ul li {
  position: relative;
  margin-left: 10px;
}

.menu ul ul {
  position: absolute;
  display: none;
  top: 100%;
  left: 0;
  z-index: 100;
  width: 200px;
  letter-spacing: 0;
}

.menu>ul li {
  float: left;
}

.menu ul li:hover>ul {
  display: block;
}

.menu ul a {
  display: block;
  position: relative;
}

.menu ul ul ul {
  top: 0;
  left: 100%;
}


/* Main Styles */

.menu ul a {
  text-decoration: none;
}

.menu ul>li>a {
  color: #333;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  overflow: hidden;
  padding: 10px 14px;
  margin-bottom: 12px;
  letter-spacing: 0.3px;
}

#current {
  background-color: #505050;
  color: #fff;
}

#current.sf-with-ul:after {
  color: #fff;
}

.menu ul ul li a,
.menu ul li.sfHover a.sf-with-ul:after,
.menu ul li a {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

ul.float-right li a {
  background-color: #f0f0f0;
  color: #333;
}

ul.float-right li a:hover,
.menu ul li.sfHover a.sf-with-ul,
.menu ul li a:hover {
  background-color: #505050;
  color: #fff;
}


/* Drop-Down */

.menu ul ul {
  background-color: #282828;
  z-index: 999;
  margin: 0 0 0 0;
  padding: 14px 0;
  border-top: 10px solid inherit;
}

.menu ul ul:before {
  font-family: "FontAwesome";
  content: "";
  font-size: 23px;
  color: #282828;
  display: block;
  position: absolute;
  top: -11px;
  left: 13px;
}

.menu ul ul ul:before {
  content: "";
}

.menu>ul ul li:hover>a {
  border-bottom: 0;
  background: transparent;
}

.menu ul ul li {
  margin: 0;
  padding: 0;
  width: 100%;
}

.menu ul li.sfHover ul li a.sf-with-ul,
.menu ul ul li a {
  font-weight: 500;
  text-transform: none;
  color: #9f9f9f;
  font-size: 14px;
  line-height: 19px;
  padding: 5px 18px !important;
  border: 0;
  margin-bottom: 0;
  background-color: #282828;
}

.menu ul ul li:first-child {
  border: none;
}

.menu ul ul li a:hover,
.menu ul li.sfHover ul li a.sf-with-ul:hover {
  color: #fff;
}

.menu ul ul ul {
  margin: 0 0 0 3px;
  border: none;
}

.sf-with-ul:after {
  content: "";
  font-family: "FontAwesome";
  font-size: 14px;
  margin: 0 0 0 7px;
  top: 0;
  position: relative;
  width: 9px;
  height: 5px;
  display: inline-block;
  color: rgba(0, 0, 0, 0.4);
  float: right;
  font-weight: 500;
}

ul li ul a.sf-with-ul:after {
  right: 9px;
  top: 4px;
  position: absolute;
  content: "";
}

.menu ul li.sfHover a.sf-with-ul:after {
  color: #fff;
}

.menu ul li ul a.sf-with-ul:after .search-container #inputseach {
  width: 44.5%;
}


/* ------------------------------------------------------------------- */


/* Mobile Navigation
---------------------------------------------------------------------- */

#mobile-navigation {
  display: none;
}

@media only screen and (max-width: 990px) {
  #mobile-navigation {
    display: block;
  }
  #responsive {
    display: none;
  }
  a.menu-trigger,
  a.menu-trigger i {
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  a.menu-trigger {
    color: #fff;
    background-color: #282828;
    display: block;
    float: left;
    z-index: 100;
    position: relative;
    margin: 0 0 30px 0;
    width: 100%;
    box-sizing: border-box;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600;
  }
  a.menu-trigger:hover {
    color: #fff;
    background-color: #505050;
  }
  a.menu-trigger i {
    font-size: 18px;
    padding: 17px 17px 18px 17px;
    margin: 0px 10px 0px 0px;
    background-color: rgba(255, 255, 255, 0.08);
    font-weight: 500;
  }
  a.menu-trigger:hover i {
    background-color: rgba(0, 0, 0, 0.08);
  }
  body[data-menu-position="open"] a.menu-trigger i.fa.fa-reorder:before {
    content: "";
  }
  body[data-menu-position="closed"] a.menu-trigger i.fa.fa-reorder:before {
    content: "";
  }
  #navigation ul.float-right {
    position: relative;
    text-transform: uppercase;
    font-weight: 600;
    display: inline-block;
    width: 100%;
    right: 0;
  }
  #navigation {
    float: none;
  }
  #navigation ul.float-right a {
    color: #333;
    margin: 20px 25px 20px 0;
    display: inline-block;
    font-size: 14px;
  }
  #navigation ul.float-right a:hover {
    color: #333;
    margin: 20px 25px 20px 0;
    display: inline-block;
    font-size: 14px;
  }
  ul.float-right li {
    display: inline-block;
  }
  #logo {
    width: 100%;
    position: relative;
    box-sizing: border-box;
    display: inline-block;
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 27px;
  }
}

#jPanelMenu-menu {
  background-color: #282828;
}

#jPanelMenu-menu #current:after,
#jPanelMenu-menu .sf-sub-indicator {
  display: none;
}

#jPanelMenu-menu li a {
  display: block;
  padding: 12px 20px !important;
  background-color: #333;
  color: #aaa;
  font-weight: 500;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  -ms-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  outline: none;
  line-height: 21px;
}

#jPanelMenu-menu #current,
#jPanelMenu-menu li a:hover {
  background-color: #505050;
  color: #fff;
}

#jPanelMenu-menu li li a {
  background-color: #282828;
  padding-left: 30px !important;
}


