/* Homepage Banner
---------------------------------------------------------------------- */

#banner {
  max-height: 640px;
  width: 100%;
  display: inline-block;
  position: relative;
  background-repeat: no-repeat;
  background-position: 50%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-color: #202020;
  background-size: cover;
  margin-bottom: 35px;
}

#banner:before {
  content: "";
  width: 100%;
  height: 100%;
  display: inline-block;
  background-color: rgba(42, 46, 50, 0.70);
  position: absolute;
}

.search-container {
  display: block;
  width: 100%;
  position: relative;
  padding: 140px 0;
  margin-top: -10px;
}

@media only screen and (min-width: 1367px) {
  .search-container {
    padding: 190px 0;
  }
}

.search-container h2 {
  font-size: 46px;
  color: #fff;
  margin-bottom: 30px;
  letter-spacing: -1px;
}

.search-container h3 {
  color: white;
}

#banner .search-container input {
  font-weight: 500;
  font-size: 18px;
  float: left;
  box-sizing: border-box;
  padding: 19px 20px;
  border: none;
  margin-right: 2%;
  width: 44.5%;
  float: left;
}


/* Form Icons */

input .ico-01 {
  background: #fff url("./images/ico-01.png") no-repeat scroll 97% 20px / 21px 21px
}

input .ico-02 {
  background: #fff url("./images/ico-02.png") no-repeat scroll 96% 20px / 14px 21px;
}

.search-container input:focus {
  border: none;
}

.search-container button {
  background-color: #505050;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 20px;
  box-sizing: border-box;
  text-align: center;
  height: 60px;
  width: 6%;
  top: 3px;
  position: relative;
}

.search-container button:before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.1);
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  z-index: 99;
}

.search-container button:hover:before {
  opacity: 1;
}

.search-container button i {
  z-index: 100;
  position: relative;
}


/* Browse Jobs */

.browse-jobs {
  color: #fff;
  margin-top: 20px;
}

.browse-jobs a {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 5px 10px;
  color: #fff;
  margin: 0 5px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.browse-jobs a:hover {
  background-color: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
}


/* Announce */

.announce {
  color: #fff;
  font-size: 24px;
  font-weight: 300;
  margin-top: 40px;
}

.announce strong {
  font-size: 30px;
  color: #fff;
}


