}

#titlebar.photo-bg .container {
  z-index: 11;
}

#titlebar.photo-bg:before {
  content: "";
  width: 100%;
  height: 100%;
  display: inline-block;
  background-color: rgba(34, 34, 34, 0.65);
  position: absolute;
  z-index: 10;
  top: 0px;
}

.pagination {
