}

#breadcrumbs.recipe-page {
  position: absolute;
  right: 10px;
  top: -120px;
  z-index: 99;
  width: 300px;
}

#breadcrumbs.recipe-page ul li:first-child {
  display: none;
}


/* ------------------------------------------------------------------- */


/* Miscellaneous
---------------------------------------------------------------------- */

.divider-line {
  border-bottom: 1px solid #e8e8e8;
}


/* ---------------------------------- */


/* Buttons
------------------------------------- */

a.button.gray.app-link.opened,
.upload-btn,
button,
input[type="button"],
input[type="submit"],
a.button {
  cursor: pointer;
  padding: 10px 20px;
