  -ms-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

button {
  padding: 12px 18px;
}

.button.gray {
  background-color: #f2f2f2;
  color: #808080;
}

a.button.white {
  background-color: #fff;
  color: #333;
}

.button.big {
  font-size: 16px;
  padding: 12px 22px;
  z-index: 0;
}

