/* Popup */

#small-dialog {
  background: #fff;
  padding: 30px 40px 35px 40px;
  text-align: left;
  max-width: 610px;
  margin: 40px auto;
  position: relative;
  box-sizing: border-box;
}

#small-dialog h3 {
  margin-bottom: 20px;
}

