/* Job List Styles
---------------------------------------------------------------------- */

.padding-right {
  padding-right: 20px;
}

.resumes-list,
.job-list {
  margin-bottom: 30px;
}

.resumes-list li,
.job-list li {
  background-color: #fff;
  border-top: 1px solid #e0e0e0;
  position: relative;
  z-index: 99;
}

.resumes-list li a,
.job-list li a {
  color: #808080;
  width: 100%;
  height: 100%;
  display: inline-block;
}

.resumes-list li:last-child,
.job-list li:last-child {
  border-bottom: 1px solid #e0e0e0;
}

.resumes-list li:nth-child(2n),
.job-list li:nth-child(2n) {
  background-color: #fbfbfb;
}

.resumes-list li:hover,
.job-list li:hover {
  background-color: rgba(80, 80, 80, 0.05);
  border-color: rgba(80, 80, 80, 0.7);
  z-index: 1011;
}

.resumes-list li.highlighted a:before,
.resumes-list li a:before,
.job-list li.highlighted a:before,
.job-list li a:before {
  content: "";
  width: 100%;
  height: 100%;
  display: inline-block;
  position: absolute;
  -webkit-box-shadow: 0px 1px 0px 0px rgba(80, 80, 80, 0.7);
  -moz-box-shadow: 0px 1px 0px 0px rgba(80, 80, 80, 0.7);
  box-shadow: 0px 1px 0px 0px rgba(80, 80, 80, 0.7);
  top: 0;
  left: 0;
  z-index: 101;
  opacity: 0;
}

.resumes-list li:hover a:before,
.job-list li:hover a:before {
  opacity: 1
}

.resume-titlebar span a:hover i,
.resumes-list li,
.resumes-list li a:before,
.job-list li,
.job-list li a:before {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}


/* Highlighted Job*/

.resumes-list li.highlighted a:before,
.job-list li.highlighted a:before {
  -webkit-box-shadow: 0px 1px 0px 0px #e0da94 !important;
  -moz-box-shadow: 0px 1px 0px 0px #e0da94 !important;
  box-shadow: 0px 1px 0px 0px #e0da94 !important;
  opacity: 1;
}

.resumes-list li.highlighted,
.job-list li.highlighted {
  background-color: #fefff6 !important;
  border-color: #e0da94 !important;
  z-index: 100;
}


/* Icons */

.resume-titlebar i,
.job-spotlight i,
.job-list li a i,
.resumes-list li a i {
  color: #999;
  margin-right: 4px;
}

.resume-titlebar span a:hover i {
  color: #505050;
}

.resume-titlebar span,
.job-spotlight span,
.job-list li a span,
.resumes-list li a span {
  margin-right: 17px;
  display: inline-block;
}

.job-icons span {
  margin-bottom: 8px;
}


/* Text */

.resumes-list li a p,
.job-list li a p {
  line-height: 27px;
  margin: 11px 0 0 0;
  padding-right: 40px;
  color: #666;
}


/* Content*/

.job-list img {
  float: left;
  display: inline-block;
  margin-left: 35px;
  margin-top: 2px;
  width: 60px;
  height: 60px;
}

.resumes-list img {
  border-radius: 50%;
