/* Hover Icon / Hover Cover
------------------------------------- */

.hover-icon {
  position: absolute;
  right: 0;
  bottom: -46px;
  color: #fff;
  font-size: 14px;
  width: 46px;
  height: 46px;
  background-color: #505050;
  border-radius: 0;
  opacity: 0;
  z-index: 99;
  visibility: hidden;
}

