/* Star Rating
------------------------------------- */

.rating {
  box-sizing: content-box;
  overflow: hidden;
  position: relative;
  width: 110px;
  height: 24px;
  display: block;
}

.rating .star-rating {
  position: relative;
  width: inherit;
  display: block;
  padding: 0;
  margin: 0;
}

.rating.five-stars .star-rating:after {
  width: 100%;
}

.rating.four-stars .star-rating:after {
  width: 80%;
}

.rating.three-stars .star-rating:after {
  width: 60%;
}

.rating.two-stars .star-rating:after {
  width: 40%;
}

.rating.one-stars .star-rating:after {
  width: 20%;
}

.rating.no-stars .star-rating:after {
  display: none;
}

.rating .star-rating:after {
  content: "    ";
  font-family: "FontAwesome";
  color: #ffc741;
  display: block;
  position: absolute;
  font-size: 19px;
  z-index: 10;
  letter-spacing: -0.5px;
}

.rating .star-bg:after {
  content: "    ";
  font-family: "FontAwesome";
  color: #c0c0c0;
  display: block;
  position: absolute;
  font-size: 19px;
  z-index: 9;
  padding: 0;
  margin: 0;
  letter-spacing: -0.5px;
}


